@import url("https://fonts.googleapis.com/css2?family=Inter&family=Lato&family=Libre+Baskerville&family=Merriweather:wght@700&family=Work+Sans&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Carter+One&display=swap");

* {
  margin: 0;
  padding: 0;
  font-family: "Work Sans", sans-serif;
}

html {
  color: rgb(96 101 123);
}

p {
  text-align: center;
}
.logomain {
  width: 18%;
}
.navbar-toggler:focus {
  box-shadow: none;
}
.navbar-toggler {
  border: none;
}
.navbar-toggler i {
  color: #fff;
}
.nav_ul li {
  padding: 0 1rem;
}
.nav_ul a {
  font-size: 1.09rem;
}
.Nav_img {
  display: contents;
}
.nav_ul {
  padding: 0 1rem;
}
.navbtn a {
  color: #ffffff;
  text-decoration: none;
  font-weight: 600;
  letter-spacing: 0.9px;
  margin: 0 0.5rem;
}
.nav_a1 {
  padding: 0.7rem 1rem;
  background-color: #2374e1;
  border-radius: 8px;
}
.nav_a2 {
  padding: 0.7rem 1rem;
  background-color: #ff0000;
  border-radius: 8px;
}
.nav_a1 i {
  padding-right: 0.3rem;
}
.nav_a2 i {
  padding-right: 0.3rem;
  font-size: 1.56rem;
}
/* .nav_a2 i {
  color: #ff0000;
} */
/* ****************************************** */
.section1 {
  background-image: url(./home\ main.jpg);
  background-size: cover;
  color: #000;
  background-blend-mode: color;
  background-color: rgba(255, 255, 255, 0.712);
  padding: 8rem 0;
}
.section1 p {
  font-weight: 600;
  font-size: 1.2rem;
}
.section1 h2 {
  text-align: center;
  font-size: 2.4rem;
  letter-spacing: 1px;
  word-spacing: 5px;
  font-family: "Carter One", cursive;
}
.section1 button {
  font-weight: 600;
  font-size: 1.3rem;
  padding: 1rem 4rem;
  background-color: #0072bc;
}
/* ****************************************** */
/* ****************************************** */
.main_btns {
  text-align: center;
}
.home-p {
  user-select: none;
}

.main-sec:focus {
  border: 0.3px solid rgb(43, 45, 56, 0.3);
  outline: none;
}

.main-sec {
  font-size: 18px;
  letter-spacing: 1px;
  font-weight: 600;
  word-spacing: 2px;
  line-height: 1.6;
  color: #000000;
  height: auto;
  padding: 2rem;
  min-height: 380px;
  border-radius: 10px 0 10px 0;
  background-color: rgba(206, 206, 206, 0.712);
}
.main_btns {
  margin-top: -3rem !important;
}
.main_btns button {
  background: transparent;
  color: #ffffff;
}
.btn-9 {
  font-size: 1.7rem;
  position: absolute;
  right: 19%;
  margin-top: -4.5rem;
  background: transparent;
}
.btn-9 i {
  font-size: 2rem;
}
.btn-11 i {
  font-size: 1.7rem;
  background: #3cb878;
  padding: 0.85rem 1rem;
  border-radius: 50%;
  margin: 0 0.5rem;
}
.btn-14 i {
  font-size: 1.7rem;
  background: #ed1878;
  padding: 0.85rem 1rem;
  border-radius: 50%;
  margin: 0 0.5rem;
}
.btn-15 i {
  font-size: 1.7rem;
  background: #2b2b2b;
  padding: 0.85rem 1rem;
  border-radius: 50%;
  margin: 0 0.5rem;
}
/* ************************************************** */

.footer {
  text-align: center;
  color: #fff;
  background-color: #000;
  margin-top: 4.5rem;
}
.footer p {
  padding: 1.5rem 0;
  margin-bottom: 0;
}
.footer a {
  text-decoration: none;
  color: #fff;
  font-size: 1.3rem;
  font-weight: 600;
}
.footer a:hover {
  border-bottom: 1px solid #fff;
}
@media only screen and (min-width: 320px) and (max-width: 576px) {
  /* .navbar > .container {
    flex-wrap: initial;
  } */
  .logomain {
    width: 60%;
  }
  h2 {
    font-size: 2rem !important;
  }
  .section1 {
    padding: 4rem 0;
  }
  .btn-9 {
    right: 15%;
    margin-top: -6.5rem;
  }
  .nav_ul {
    padding: 1rem 0 0.5rem 0;
  }
  .navbtn {
    padding: 1rem 0;
  }
}
